.dark{
    background-color: black;
    color:aquamarine;
    width: 90%;
    margin-left: 50px;
    margin-top: 20px;
    height: 80%;
}

.light{
    background-color: white;
    color:black;
    width: 90%;
    margin-left: 50px;
    margin-top: 20px;
    height: 80%;
}
.main{
    width: 90%;
    height: 80%;
    margin-left: 30px;
    padding-left: 40px;
    margin-top: 10px;
    background-image: linear-gradient(135deg, #868f96 10%, #a8ccd8 100%);
} 
.heading{
    font-size: 3rem;
    font-weight: 650;
    color:rgb(20, 17, 230);
    padding-top: 40px;
}
p,h1{
    margin-left: 30px;
    padding-left: 40px;
    padding-bottom: 40px;
    width: 90%;
    height: 80%;
}
button{
  width: 15%;
  height: 40px;
  background-image: linear-gradient(135deg, #25d9e2 10%, #62d90d 100%);
  border-radius: 15px;
  margin-bottom: 20px;
  font-weight: bold;
}